






































































import { Component, Prop, Vue } from "vue-property-decorator";

import CommonErrorsList from "@/components/CommonErrorsList.vue";
import CommonLoader from "@/components/CommonLoader.vue";
import User from "@/store/modules/User";
import { isArrayOfStrings } from "@/utils/ArrayUtils";

export enum AuthPageMode {
  Register = "Register",
  Login = "Login"
}

@Component({
  components: {
    CommonLoader,
    CommonErrorsList
  }
})
export default class AuthPage extends Vue {
  @Prop({ required: true }) mode!: AuthPageMode;

  isLoading = false;

  username = "";
  email = "";
  password = "";

  errors?: string[] = [];

  mounted(): void {
    this.errors = [];
  }

  get hasErrors(): boolean {
    return !!this.errors?.length;
  }

  get isRegisterMode(): boolean {
    return this.mode === AuthPageMode.Register;
  }

  async authAction(): Promise<void> {
    this.errors = [];

    this.isLoading = true;
    try {
      this.isRegisterMode
        ? await User.register({
            email: this.email,
            password: this.password,
            username: this.username
          })
        : await User.login({ email: this.email, password: this.password });
      this.$router.push({ name: this.$routesNames.home });
    } catch (e) {
      if (isArrayOfStrings(e)) {
        this.errors = e;
      } else {
        throw e;
      }
    } finally {
      this.isLoading = false;
    }
  }
}
